'use client';
import React, { useState } from 'react';
import Footer from '@/components/footer/page';
import Header from '@/components/header/page';

interface Order {
  id: string;
  customerName: string;
  date: string;
  total: number;
  status: string;
}

export default function OrderDashboard() {
  const [orders] = useState<Order[]>([
    { id: '1', customerName: 'John Doe', date: new Date().toLocaleDateString(), total: 150.0, status: 'Pending' },
    { id: '2', customerName: 'Jane Smith', date: new Date().toLocaleDateString(), total: 200.5, status: 'Completed' },
    { id: '3', customerName: 'Michael Brown', date: new Date().toLocaleDateString(), total: 50.75, status: 'Cancelled' },
    { id: '4', customerName: 'Emily Davis', date: new Date().toLocaleDateString(), total: 300.0, status: 'Completed' },
    { id: '5', customerName: 'Chris Wilson', date: new Date().toLocaleDateString(), total: 120.0, status: 'Pending' },
  ]);

  const [activeTab, setActiveTab] = useState('All');

  const filterOrders = (status: string) => {
    if (status === 'All') return orders;
    if (status === 'History') return orders.filter(order => order.status !== 'Pending');
    return orders.filter(order => order.status === status);
  };

  const tabs = ['All', 'Pending', 'Completed', 'History'];

  return (
    <div className="flex flex-col min-h-screen bg-white-100">
      {/* Header */}
      <header className="bg-white shadow">
        <Header />
      </header>

      {/* Main Content */}
      <main className="flex-grow p-6">
        <h1 className="text-2xl font-bold mb-6 text-center">Order Dashboard</h1>

        {/* Tabs Navigation */}
        <div className="flex justify-center mb-6">
          {tabs.map((tab) => (
            <button
              key={tab}
              onClick={() => setActiveTab(tab)}
              className={`px-4 py-2 mx-2 rounded-lg font-medium ${
                activeTab === tab
                  ? 'bg-blue-500 text-white'
                  : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
              }`}
            >
              {tab} Orders
            </button>
          ))}
        </div>

        {/* Orders Table */}
        <div className="overflow-x-auto">
          <table className="table-auto w-full bg-white rounded-lg shadow">
            <thead className="bg-gray-200">
              <tr>
                <th className="px-4 py-2 text-left">Order ID</th>
                <th className="px-4 py-2 text-left">Customer Name</th>
                <th className="px-4 py-2 text-left">Date</th>
                <th className="px-4 py-2 text-left">Total</th>
                <th className="px-4 py-2 text-left">Status</th>
              </tr>
            </thead>
            <tbody>
              {filterOrders(activeTab).length > 0 ? (
                filterOrders(activeTab).map((order) => (
                  <tr key={order.id} className="border-b">
                    <td className="px-4 py-2">{order.id}</td>
                    <td className="px-4 py-2">{order.customerName}</td>
                    <td className="px-4 py-2">{order.date}</td>
                    <td className="px-4 py-2">${order.total.toFixed(2)}</td>
                    <td className="px-4 py-2">
                      <span
                        className={`px-2 py-1 rounded-full text-sm ${
                          order.status === 'Completed'
                            ? 'bg-green-100 text-green-600'
                            : order.status === 'Pending'
                            ? 'bg-yellow-100 text-yellow-600'
                            : 'bg-red-100 text-red-600'
                        }`}
                      >
                        {order.status}
                      </span>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={5} className="text-center py-4">
                    No orders found.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-white shadow mt-4">
        <Footer />
      </footer>
    </div>
  );
}
